<template>
  <div>
    <b-row>
      <!-- progressbar -->
      <div class="col-md-12">
        <div class="progressbar-container text-center">
          <ul class="progressbar">
            <li class="progressbar-done">檢視購物車</li>
            <li class="progressbar-done">付款方式及寄送資訊</li>
            <li class="progressbar-active">確認訂單資料</li>
            <li>訂單成立</li>
          </ul>
        </div>
      </div>
      <!-- progressbar -->
    </b-row>

    <b-row class="mt-3">
      <b-col md="12">
        <b-card
          header="確認購物清單"
          header-text-variant="dark"
          header-tag="header"
          header-bg-variant="muted"
        >
          <b-table
            striped
            hover
            stacked="lg"
            ref="selectableTable"
            :fields="fields"
            :items="items"
          >
            <!-- Optional default data cell scoped slot -->
            <template v-slot:cell()="data">
              <span v-text="data.value"></span>
            </template>
            <!-- A custom formatted column -->
            <template v-slot:cell(name)="data">
              <router-link
                class="text-decoration-none text-primary"
                :to="'/product/' + data.item.isbn"
              >
                <span v-text="data.value"></span>
              </router-link>
            </template>
            <!-- A custom formatted column -->
            <template v-slot:cell(salePrice)="data">
              <span v-text="salePrice(data)"></span>
            </template>
            <!-- A custom formatted column -->
            <template v-slot:cell(subtotal)="data">
              <span v-text="calSubTotal(data)"></span>
            </template>
          </b-table>
          <div class="d-flex bg-light">
            <div class="ml-auto">
              <p class="mt-3 mr-3">
                共
                <span class="text-danger">{{ items.length }}</span> 項商品，數量
                <span class="text-danger" v-text="totalQuantity()"></span>
                個，總金額 NT$
                <span class="text-danger" v-text="totalAmount()"></span> 元
              </p>
            </div>
          </div>
        </b-card>
      </b-col>
    </b-row>

    <b-row class="mt-3">
      <b-col md="12">
        <b-card
          header="付款方式及寄送資訊"
          header-text-variant="dark"
          header-tag="header"
          header-bg-variant="muted"
        >
          <b-row class>
            <b-col md="2" class="d-flex align-items-center ml-1">
              <label for="deliver">配送方式</label>
            </b-col>
            <b-col md="9" class="ml-1">
              <span id="deliver">{{ getDeliver }}</span>
            </b-col>
          </b-row>

          <b-row class>
            <b-col md="2" class="d-flex align-items-center ml-1">
              <label for="payment">付款方式</label>
            </b-col>
            <b-col md="9" class="ml-1">
              <span id="payment">{{ getPayment }}</span>
            </b-col>
          </b-row>

          <b-row class>
            <b-col md="2" class="d-flex align-items-center ml-1">
              <label for="invoiceType">發票資訊</label>
            </b-col>
            <b-col md="9" class="ml-1">
              <span id="invoiceType">{{ getInvoiceType }}</span>
            </b-col>
          </b-row>

          <b-row class>
            <b-col md="2" class="d-flex align-items-center ml-1">
              <label for="buyerName">訂購人</label>
            </b-col>
            <b-col md="9" class="ml-1">
              <span id="buyerName" v-text="getBuyer.name"></span>
            </b-col>
          </b-row>

          <b-row class>
            <b-col md="2" class="d-flex align-items-center ml-1">
              <label for="buyerContactNumber">訂購人聯絡電話</label>
            </b-col>
            <b-col md="9" class="ml-1">
              <span
                id="buyerContactNumber"
                v-text="getBuyer.contactNumber"
              ></span>
            </b-col>
          </b-row>

          <b-row class>
            <b-col md="2" class="d-flex align-items-center ml-1">
              <label for="buyerMobile">訂購人手機號碼</label>
            </b-col>
            <b-col md="9" class="ml-1">
              <span id="buyerMobile" v-text="getBuyer.mobile"></span>
            </b-col>
          </b-row>

          <b-row class>
            <b-col md="2" class="d-flex align-items-center ml-1">
              <label for="buyerAddress">訂購人地址</label>
            </b-col>
            <b-col md="9" class="ml-1">
              <span id="buyerAddress" v-text="getBuyer.address"></span>
            </b-col>
          </b-row>

          <b-row class>
            <b-col md="2" class="d-flex align-items-center ml-1">
              <label for="receiverName">收件人</label>
            </b-col>
            <b-col md="9" class="ml-1">
              <span id="receiverName" v-text="getReceiverName"></span>
            </b-col>
          </b-row>

          <b-row class>
            <b-col md="2" class="d-flex align-items-center ml-1">
              <label for="receiverContactNumber">收件人聯絡電話</label>
            </b-col>
            <b-col md="9" class="ml-1">
              <span id="receiverContactNumber">{{ getContactNumber }}</span>
            </b-col>
          </b-row>

          <b-row class>
            <b-col md="2" class="d-flex align-items-center ml-1">
              <label for="receiverMobile">收件人手機號碼</label>
            </b-col>
            <b-col md="9" class="ml-1">
              <span id="receiverMobile">{{ getMobile }}</span>
            </b-col>
          </b-row>

          <b-row class>
            <b-col md="2" class="d-flex align-items-center ml-1">
              <label for="receiverAddress">收件人地址</label>
            </b-col>
            <b-col md="9" class="ml-1">
              <span id="receiverAddress">{{ getAddress }}</span>
            </b-col>
          </b-row>
        </b-card>

        <div class="d-flex justify-content-end mt-3">
          <!-- <b-button class="mr-3" variant="outline-primary" @click="showModal">
            modal
          </b-button> -->
          <b-button class="mr-3" variant="outline-primary" @click="goDeliver()">
            <font-awesome-icon
              :icon="['fas', 'angle-double-left']"
            />&nbsp;上一步
          </b-button>
          <b-button class="mr-3" variant="outline-primary" @click="checkout">
            確認送出&nbsp;
            <font-awesome-icon :icon="['fas', 'angle-double-right']" />
          </b-button>
        </div>
      </b-col>
    </b-row>

    <b-modal ref="order-modal" hide-footer title="Demo Site" @hide="backToHome">
      <div class="d-block text-center">
        <!-- <p>訂單編號 {{ orderNo }}</p>
        <p>訂單日期 {{ orderDate }}</p>
        <p>訂單金額 NT$ {{ orderAmount }}</p> -->
        <p>
          感謝您的訂購，您已經完成本筆交易的訂購程序，我們將會盡快為您處理訂單並安排出貨。
        </p>
        <b-table stacked :items="orderItems"></b-table>
        <p>
          <a
            href="javascript:void(0)"
            class="text-decoration-none"
            @click="orderDetail"
            >查看訂單詳情</a
          >
        </p>
        <!-- <img src="./../assets/img/QRCode.png" alt="QRCode" /> -->
        <img :src="getQRCode" alt="QRCode" />
      </div>
    </b-modal>
  </div>
</template>
 
<script>
import { mapGetters } from "vuex";
// import buyer from "@/api/buyer.js";
import config from "@/common/config";
import styles from "@/assets/css/progress-bar.css";
import { library } from "@fortawesome/fontawesome-svg-core";
import {
  faAngleDoubleRight,
  faAngleDoubleLeft,
} from "@fortawesome/free-solid-svg-icons";
import ApiService from "@/common/api.service";
import util from "@/common/util";
import { JWT_EXPIRED_MSG } from "@/common/constant";

library.add(faAngleDoubleRight, faAngleDoubleLeft);

export default {
  name: "Confirm",
  data() {
    return {
      // https://bootstrap-vue.org/docs/components/table#row-select-support
      // selectMode: "multi",
      // onRowSelected(items) {
      //   this.selected = items
      // },
      fields: config.confirmFields,
      // buyer: null,
      orderNo: "20201130092604879QTP",
      orderDate: "2020-11-30 09:26:05",
      orderAmount: 2127,
      orderItems: [],
      QRCode: "",
    };
  },
  components: {},
  computed: {
    items() {
      return this.$store.getters["cart/cartProducts"];
    },
    getDeliver() {
      // return this.$store.getters["deliver/getState"].deliver;
      return this.getState().deliver === "homeDelivery" ? "宅配" : "郵寄";
    },
    getPayment() {
      // return this.$store.getters["deliver/getState"].payment === "COD";
      return this.getState().payment === "COD" ? "貨到付款" : "";
    },
    getInvoiceType() {
      // return this.$store.getters["deliver/getState"].invoiceType === "paper";
      return this.getState().invoiceType === "paper" ? "紙本發票" : "";
    },
    getReceiverName() {
      if (this.isReceiverInfoNew()) {
        return this.getState().receiverName;
      } else {
        return this.getBuyer.name;
      }
    },
    getContactNumber() {
      if (this.isReceiverInfoNew()) {
        let ext =
          this.getState().receiverExt.length == 0
            ? ""
            : " 分機 " + this.getState().receiverExt;
        return (
          this.getState().receiverArea +
          "-" +
          this.getState().receiverPhone +
          ext
        );
      } else {
        return this.getBuyer.contactNumber;
      }
    },
    getMobile() {
      if (this.isReceiverInfoNew()) {
        return this.getState().receiverMobile;
      } else {
        return this.getBuyer.mobile;
      }
    },
    getAddress() {
      if (this.isReceiverInfoNew()) {
        return (
          this.getState().zipcode +
          this.getState().county +
          this.getState().district +
          this.getState().receiverAddress
        );
      } else {
        return this.getBuyer.address;
      }
    },
    ...mapGetters("auth", ["getBuyer"]),
    getQRCode() {
      return this.QRCode;
    },
  },
  methods: {
    ...mapGetters("deliver", ["getState"]),
    isReceiverInfoNew() {
      return this.getState().receiverInfo === "new";
    },
    // fetchData() {
    //   this.buyer = buyer;
    // },
    goDeliver() {
      this.$router.push({ name: "deliver" });
    },
    salePrice(data) {
      // return Math.round(data.item.listPrice * data.item.discount / 100);
      data.item.salePrice = this.calSalePrice(
        data.item.listPrice,
        data.item.discount
      );
      return data.item.salePrice;
    },
    calSalePrice(price, discount) {
      return Math.round((price * discount) / 100);
    },
    calSubTotal(data) {
      // return this.salePrice(data) * data.item.quantity;
      return data.item.salePrice * data.item.quantity;
    },
    totalQuantity() {
      //https://fred-zone.blogspot.com/2017/01/javascript-mapreduce.html
      //https://developer.mozilla.org/zh-TW/docs/Web/JavaScript/Reference/Global_Objects/Array/map
      //https://developer.mozilla.org/zh-TW/docs/Web/JavaScript/Reference/Global_Objects/Array/Reduce
      return this.items
        .map((item) => parseInt(item.quantity, 10))
        .reduce((acc, cur) => acc + cur, 0);
    },
    totalAmount() {
      // return this.items.map(item => Math.round(item.listPrice * item.discount / 100) * parseInt(item.quantity, 10)).reduce((acc, cur) => acc + cur, 0);
      return this.items
        .map(
          (item) =>
            this.calSalePrice(item.listPrice, item.discount) *
            parseInt(item.quantity, 10)
        )
        .reduce((acc, cur) => acc + cur, 0);
    },
    createReqObj() {
      let bookArray = this.items.map(function (item) {
        let obj = {};
        obj["isbn"] = item.isbn;
        obj["quantity"] = item.quantity;
        return obj;
      });
      let deliver = JSON.parse(JSON.stringify(this.getState()));
      let keyArray = [
        "receiverNameState",
        "receiverAreaState",
        "receiverPhoneState",
        "receiverExtState",
        "receiverMobileState",
        "receiverAddressState",
      ];
      for (let value of keyArray) {
        // console.log(value);
        delete deliver[value];
      }
      console.log(JSON.stringify(bookArray), JSON.stringify(deliver));
      let reqObj = {};
      reqObj["bookList"] = bookArray;
      reqObj["deliver"] = deliver;
      return reqObj;
    },
    checkout() {
      let reqObj = this.createReqObj();
      console.log(JSON.stringify(reqObj));
      ApiService.setHeader();
      ApiService.post("order/create", reqObj)
        .then((response) => {
          console.log(
            "Confirm.vue checkout response : " + JSON.stringify(response)
          );
          ApiService.deleteHeader();
          this.$store.dispatch("cart/clearState");
          this.$store.dispatch("deliver/clearState");

          this.completeOrder(response.data);
          this.$router.push({ name: "complete" });

          // this.$store.dispatch("deliver/refresh");
          // this.showOrder(response.data);
          // this.$bvModal
          //   .msgBoxOk("訂單成立", {
          //     title: "Demo Site",
          //     size: "sm",
          //     buttonSize: "sm",
          //     okVariant: "success",
          //     headerClass: "p-2 border-bottom-0",
          //     footerClass: "p-2 border-top-0",
          //     centered: true,
          //   })
          //   .then((value) => {
          //     console.log("msgBoxOk", value);
          //   })
          //   .catch((err) => {
          //     console.log("msgBoxErr", err);
          //   });
        })
        .catch((err) => {
          console.log("err : " + err);
          console.log("err JSON.stringify : " + JSON.stringify(err));
          console.log("err.response : " + JSON.stringify(err.response));
          ApiService.deleteHeader();
          let msg = "";
          if (err.response.data.errors) {
            msg = "數量不可為 0 , 請修改訂單";
            console.log("err.response.data.errors", err.response.data.errors);
          } else {
            console.log("err.response.data.message", err.response.data.message);
            msg = util.checkJWTExpired(err, this);
            // if (util.testJWTExpiredMsg(err.response.data.message)) {
            //   msg = JWT_EXPIRED_MSG;
            //   this.$store.dispatch("auth/purgeAuth");
            //   console.log("this.$route.path", this.$route.path);
            //   // https://router.vuejs.org/zh/guide/essentials/navigation.html
            //   this.$router.push({
            //     path: "login",
            //     query: { redirect: this.$route.path },
            //   });
            // } else {
            //   msg = err.response.data.message;
            // }
          }
          this.$store.dispatch("modal/setModalMsg", msg);
          this.$store.dispatch("modal/setModalShow", true);
        });
    },
    completeOrder(order) {
      let obj = {
        訂單編號: order.orderNo,
        訂單日期: order.orderDate,
        訂單金額: order.orderAmount,
      };
      let itemArray = [obj];
      this.$store.dispatch("complete/setOrderItems", itemArray);
      this.$store.dispatch("complete/setQRCode", order.qrcode);
      this.$store.dispatch("complete/setOrderNo", order.orderNo);
    },
    showOrder(order) {
      this.orderNo = order.orderNo;
      let obj = {
        訂單編號: order.orderNo,
        訂單日期: order.orderDate,
        訂單金額: order.orderAmount,
      };
      this.QRCode = order.qrcode;
      let itemArray = [obj];
      this.orderItems = JSON.parse(JSON.stringify(itemArray));
      this.$refs["order-modal"].show();
    },
    showModal() {
      let obj = {
        訂單編號: this.orderNo,
        訂單日期: this.orderDate,
        訂單金額: this.orderAmount,
      };
      let itemArray = [obj];
      this.orderItems = JSON.parse(JSON.stringify(itemArray));
      this.$refs["order-modal"].show();
    },
    orderDetail() {
      this.$router
        .push({ path: `/order/${this.orderNo}` })
        .catch((err) => console.log(err));
    },
    backToHome() {
      this.$router.push({ name: "index" });
    },
  },
  mounted() {},
  created() {
    // this.fetchData();
  },
};
</script>
<style>
.bg-LightSteelBlue {
  background-color: LightSteelBlue !important;
}
</style>